@import url(https://fonts.googleapis.com/css?family=Montserrat:400,600|Open+Sans:400,700&display=swap&subset=cyrillic-ext);
:root {
  --button-secondary-color: #6A6A6A;
  --button-secondary-color-hover: #464646;
  --link-color: #0C0033;

  --label-color: #87819A;
  --input-color: #0C0033;
  --input-placeholder-color: #C3C0CD;
  --input-border-color: #C3BFCC;

  --input-label-block-bg: #E8E6EB;
  --input-label-block-color: #93939B;

  --disabled-input-bg: #E7E5EB;
  --disabled-input-color:#B0ABBD;

  --active-control-border: #4000FF;

  --font-heading: 'Montserrat', sans-serif;
  --font-text: 'Open Sans', sans-serif;
}
a {
  color: var(--link-color);
}

h2.ui.header {
  font-family: var(--font-heading);
  font-size: 18px;
  font-weight: 600;
  color: #373737;
}

h3.ui.header {
  font-family: var(--font-heading);
  font-size: 18px;
  font-weight: 400;
  color: #51535C;
}

input:-moz-read-only,
.ui .form .field input:-moz-read-only,
.ui .form .field input:-moz-read-only:focus {
  background: var(--disabled-input-bg);
  color: var(--disabled-input-color);
  border-color: var(--disabled-input-bg);
}

input:read-only,
.ui .form .field input:read-only,
.ui .form .field input:read-only:focus {
  background: var(--disabled-input-bg);
  color: var(--disabled-input-color);
  border-color: var(--disabled-input-bg);
}

.ui.form .field input:-moz-read-only {
  cursor: auto;
}

.ui.form .field input:read-only {
  cursor: auto;
}

.ui.form .field input:-moz-read-only:focus {
  border-color: var(--input-border-color);
}

.ui.form .field input:read-only:focus {
  border-color: var(--input-border-color);
}

.ui.form .field input:not(:-moz-read-only),
.ui.form .field textarea,
.ui.selection.dropdown > .text:not(.default){
  font-family: var(--font-heading);
  color: var(--input-color);
  font-weight: 600;
}

.ui.form .field input:not(:read-only),
.ui.form .field textarea,
.ui.selection.dropdown > .text:not(.default){
  font-family: var(--font-heading);
  color: var(--input-color);
  font-weight: 600;
}

.ui.form .field input:not(:read-only)::-webkit-input-placeholder,
.ui.form .field textarea::-webkit-input-placeholder {
  font-family: var(--font-text);
  font-weight: 400;
  opacity: 0.25;
}

.ui.form .field input:not(:read-only)::-moz-placeholder,
.ui.form .field textarea::-moz-placeholder {
  font-family: var(--font-text);
  font-weight: 400;
  opacity: 0.25;
}

.ui.form .field input:not(:read-only):-ms-input-placeholder,
.ui.form .field textarea:-ms-input-placeholder {
  font-family: var(--font-text);
  font-weight: 400;
  opacity: 0.25;
}

.ui.form .field input:not(:read-only)::-ms-input-placeholder,
.ui.form .field textarea::-ms-input-placeholder {
  font-family: var(--font-text);
  font-weight: 400;
  opacity: 0.25;
}

.ui.form .field input:not(:read-only)::placeholder,
.ui.form .field textarea::placeholder {
  font-family: var(--font-text);
  font-weight: 400;
  opacity: 0.25;
}

.ui.form .field input:not(:-moz-read-only):focus,
.ui.form .field textarea:not(:-moz-read-only):focus,
.ui.selection.active.dropdown,
.ui.selection.active.dropdown:hover,
.ui.selection.active.dropdown .menu,
.ui.selection.active.dropdown:hover .menu {
  border-color: var(--active-control-border);
}

.ui.form .field input:not(:read-only):focus,
.ui.form .field textarea:not(:read-only):focus,
.ui.selection.active.dropdown,
.ui.selection.active.dropdown:hover,
.ui.selection.active.dropdown .menu,
.ui.selection.active.dropdown:hover .menu {
  border-color: var(--active-control-border);
}

.ui.form textarea {
  resize: none;
}

.ui.selection.dropdown {
  border-radius: 20px;
}

.ui.form .fields .field .ui.input input,
.ui.form .field .ui.input input {
  width: 100%;
  flex: 1 1;
}

.ui.form .field > label {
  font-family: var(--font-heading);
  color: var(--label-color);
  font-size: 14px;
  font-weight: 600;
}

.ui.form .required.fields:not(.grouped) > .field>label:after,
.ui.form .required.fields.grouped > label:after,
.ui.form .required.field:not(.label--left)>label:after {
  float: right;
}

.ui.button.secondary {
  background-color: var(--button-secondary-color);
  font-family: var(--font-text);
  font-weight: 700;
  font-size: 14px;
  padding: 15px 32px;
}

.ui.button.secondary:hover {
  background-color: var(--button-secondary-color-hover);
}
/* typography */
.fw_400,
.ui.fw_400 {
  font-weight: 400;
}

/* display */
.d_flex {
  display: flex;
}

.ai_center {
  align-items: center;
}

/* paddings, margins */
.m_0,
.ui.m_0 {
  margin: 0;
}

.style_header__14a2R {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 10px 30px;
}

.style_back_link__3eNy0 {
  margin-right: 7px;
}
.ui.basic.button.style_radio_btn__1_vtH {
  padding: 11px 30px;
  border: 1px solid var(--input-border-color);
  border-radius: 30px;
  font-family: var(--font-text);
  font-size: 12px;
  color: #0C0033;
  cursor: pointer;
  margin-right: 10px;
  margin-bottom: 10px;
  box-shadow: none;
}
